   .popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .popup-content {
    background-color: white;
    padding: 20px;
    border-radius: 5px;
    width: 95vw;
    height: 95vh;
    display: flex;
    flex-direction: column;
  }
  
  .fault-groups {
    display: flex;
    flex-wrap: nowrap;
    overflow-x: auto;
    gap: 15px;
    padding-bottom: 15px; /* Add some padding for the scrollbar */
  }
  
  .fault-group {
    border: 1px solid #ccc;
    padding: 10px;
    border-radius: 5px;
    flex: 0 0 auto;
    width: 210px;
  }
  
  .group-checkbox {
    margin-bottom: 10px;
  }
  
  .code-checkboxes {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: 5px;
    max-height: calc(95vh - 150px);
    overflow-y: auto;
  }
  
  .code-checkbox {
    display: flex;
    align-items: center;
    position: relative;
  }
  
  .code-checkbox input[type="checkbox"] {
    margin-right: 5px;
    position: relative;
    z-index: 1;
  }
  
  .code-checkbox input[type="checkbox"]::before,
  .code-checkbox input[type="checkbox"]::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  pointer-events: none;
}
  
  label {
    margin-left: 5px;
    cursor: pointer;
  }
  
  .exclude-faults-btn {
    background-color: #ce83b2;
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    border-radius: 4px;
  }

  .exclude-faults-btn:hover {
    opacity: 0.8;
  }

  .popup-content {
    max-width: 90%;
  }

  .popup-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  h2 {
    margin: 0 0 10px 0;
  }
  
  .fault-code-exclusion {
    margin: 10px 0;
  }

  .fault-groups::-webkit-scrollbar {
    height: 8px;
  }
  
  .fault-groups::-webkit-scrollbar-thumb {
    background-color: #888;
    border-radius: 4px;
  }
  
  .fault-groups::-webkit-scrollbar-track {
    background-color: #f1f1f1;
  }