.App {
  text-align: center;
}

h1 {
  text-align: center;
}

/* Cover page SP Train and C Train Button styles */
.train-selection {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 40px 0;
}

.train-button {
  padding: 20px 40px;
  font-size: 24px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.train-button:hover {
  background-color: #45a049;
}

/* Import from Excel Button styles */
.import-container {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 20px;
}

.import-button {
  background-color: #4CAF50;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}

/* Filters styles */
.filters {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-bottom: 20px;
}

.filter-dropdown {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.filter-dropdown label {
  margin-bottom: 5px;
  font-weight: bold;
}

.filter-dropdown select {
  padding: 5px;
  border-radius: 4px;
  border: 1px solid #ddd;
  text-align-last: center;
}

/* Reset Filters Button styles */
.reset-button {
  align-self: flex-end;
  padding: 5px 10px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
}

/* Tasktable styles */
.table-responsive {
  overflow-x: auto;
  max-width: 100%;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px auto;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
  white-space: nowrap;
}

th {
  background-color: #f2f2f2;
  font-weight: bold;
}

/* Alert Function styles */
.alert-button {
  background-color: #f44336;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}

.alert-table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  margin-bottom: 20px;
}

.alert-table th, .alert-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.alert-table th {
  background-color: #f2f2f2;
  font-weight: bold;
}

/* Count function in Alert Table styles */
.clickable-count {
  cursor: pointer;
  color: blue;
  text-decoration: underline;
}

.clickable-count:hover {
  color: darkblue;
}

/* Back to Train Selection Button styles */
.back-button {
  background-color: #f0ad4e;
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}

@media screen and (max-width: 600px) {
  th, td {
    font-size: 14px;
    padding: 6px;
  }
}

.row-count-box {
  position: fixed;
  top: 20px;
  right: 20px;
  background-color: #f0f0f0;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.row-count-box p {
  margin: 0;
  font-weight: bold;
}

/* System Login styles */
.login-container {
  max-width: 300px;
  margin: 100px auto;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.login-container h2 {
  text-align: center;
  margin-bottom: 20px;
}

.login-container form div {
  margin-bottom: 10px;
}

.login-container label {
  display: block;
  margin-bottom: 5px;
}

.login-container input {
  width: 100%;
  padding: 5px;
}

.login-container button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.login-container .error {
  color: red;
  margin-top: 10px;
}

.logout-button {
  background-color: #dc3545;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 4px;
}

/* All Buttons hover styles (Except cover page buttons)*/
.import-button:hover, .alert-button:hover, .back-button:hover, .logout-button:hover, .reset-button:hover {
  opacity: 0.8;
}
